:root {
	font-size: 16px;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.about {
	padding: 2rem 1rem;
	margin-top: 1rem;
	background-color: rgb(245, 245, 245);
	display: grid;
	gap: 2rem;
}

.about h3 {
	margin-bottom: 0.5rem;
}

.about section p {
	line-height: 1.5;
}

a {
	color: rgb(0, 0, 180);
}

@media only screen and (min-width: 768px) {
	.about {
		padding: 2rem 10rem;
	}
}

@media only screen and (min-width: 992px) {
	.about {
		padding: 2rem 15rem;
	}
}

@media only screen and (min-width: 1200px) {
	.about {
		padding: 2rem 20rem;
	}
}
